// @flow

export interface INodeType {
  id: string;
  types: string[];
}

type NodeTypeParams = {
  id: string,
  types: string[]
};

export class NodeType implements INodeType {
  id: string;
  types: string[];

  constructor(params: NodeTypeParams) {
    this.id = params.id;
    this.types = params.types;
  }

  nodes(): NodeList<HTMLElement> {
    return document.querySelectorAll(this.types.join(', '));
  }
}
